import React from 'react';
import { tableSettingType } from '../pages/task1';
import { Button, Drawer, TextField } from '@mui/material';
import styled from 'styled-components';
import { castToNumber } from '../utils/castToNumber';

type Error = { id: number; message: string | null }[];

interface MyDrawerProp {
  tableSetting: tableSettingType | null;
  open: boolean;
  changeTableSetting: () => void;
  onClose: () => void;
}

const MyDrawer = ({ tableSetting, open, changeTableSetting, onClose }: MyDrawerProp) => {
  const [workerCount, setWorkerCount] = React.useState<string | number | null>(
    tableSetting ? tableSetting.rowCounts : null,
  );
  const [workCount, setWorkCount] = React.useState<string | number | null>(
    tableSetting ? tableSetting.columnCounts : null,
  );
  const [errors, setErrors] = React.useState<Error>([
    { id: 1, message: null },
    { id: 2, message: null },
  ]);

  const validationField = React.useCallback((value: string | number | null, fieldId: number, messageValue) => {
    setErrors((prevState) =>
      prevState.map((error) => {
        if (error.id === fieldId && !castToNumber(value)) {
          return { ...error, message: messageValue };
        } else if (error.id === fieldId && castToNumber(value)) {
          return { ...error, message: null };
        }
        return { ...error };
      }),
    );
  }, []);
  console.log(errors);
  return (
    <>
      <Drawer open={open} anchor="right" sx={{ minWidth: 300 }}>
        <DrawerTitel>Наcтройки таблицы</DrawerTitel>
        <TextFieldStyled
          error={!!errors[0].message}
          helperText={errors[0].message}
          id="standard-basic"
          label="Введите количество работников"
          variant="standard"
          value={workerCount}
          onChange={(e) => setWorkerCount(e.target.value)}
          onBlur={() => validationField(workerCount, 1, 'Количество работников должно быть целым числом')}
        />
        <TextFieldStyled
          error={!!errors[1].message}
          helperText={errors[1].message}
          id="standard-basic"
          label="Введите количество работ"
          variant="standard"
          value={workCount}
          onChange={(e) => setWorkCount(e.target.value)}
          onBlur={() => validationField(workCount, 2, 'Количество работников должно быть целым числом')}
        />
        <ButtonGroup>
          <Button variant="contained" color="success" onClick={onClose}>
            Закрыть
          </Button>
          {/*@ts-ignore*/}
          <Button variant="contained" color="success" onClick={changeTableSetting}>
            Сохранить
          </Button>
        </ButtonGroup>
      </Drawer>
    </>
  );
};
const DrawerTitel = styled.h3`
  text-align: center;
`;
const ButtonGroup = styled.div`
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
`;
const TextFieldStyled = styled(TextField)`
  margin: 10px 15px !important;
  padding: 0 10px !important;
`;
export default MyDrawer;
