import React from 'react';
// import { Column } from 'react-table';
// import MyTable from '../../components/Table';
import { DataGrid, GridColumns, GridRowsProp } from '@mui/x-data-grid';
import { randomCreatedDate, randomTraderName, randomUpdatedDate } from '@mui/x-data-grid-generator';
import { Button } from '@mui/material';
import MyDrawer from '../../components/MyDrawer';

//type Row = { col1: React.ReactNode; col2: React.ReactNode };

export type tableSettingType = {
  rowCounts: number;
  columnCounts: number;
  maximumRange: number;
};

const Task1 = () => {
  const [data, setData] = React.useState([]);
  const [tableSetting, setTableSetting] = React.useState<tableSettingType | null>(null);
  const [open, setOpen] = React.useState(false);
  const onSave = React.useCallback((setting) => setTableSetting(setting), [setTableSetting]);

  const columns: GridColumns = [
    { field: 'name', headerName: 'Name', width: 180, editable: true },
    { field: 'age', headerName: 'Age', type: 'number', editable: true },
    {
      field: 'dateCreated',
      headerName: 'Date Created',
      type: 'date',
      width: 180,
      editable: true,
    },
    {
      field: 'lastLogin',
      headerName: 'Last Login',
      type: 'dateTime',
      width: 220,
      editable: true,
    },
  ];

  const rows: GridRowsProp = [
    {
      id: 1,
      name: randomTraderName(),
      age: 25,
      dateCreated: randomCreatedDate(),
      lastLogin: randomUpdatedDate(),
    },
    {
      id: 2,
      name: randomTraderName(),
      age: 36,
      dateCreated: randomCreatedDate(),
      lastLogin: randomUpdatedDate(),
    },
    {
      id: 3,
      name: randomTraderName(),
      age: 19,
      dateCreated: randomCreatedDate(),
      lastLogin: randomUpdatedDate(),
    },
    {
      id: 4,
      name: randomTraderName(),
      age: 28,
      dateCreated: randomCreatedDate(),
      lastLogin: randomUpdatedDate(),
    },
    {
      id: 5,
      name: randomTraderName(),
      age: 23,
      dateCreated: randomCreatedDate(),
      lastLogin: randomUpdatedDate(),
    },
  ];
  console.log(data);
  // @ts-ignore
  return (
    <>
      <div style={{ height: 400, width: '100%' }}>
        <DataGrid
          rows={rows}
          columns={columns}
          autoHeight={true}
          pagination={undefined}
          hideFooterPagination={true}
          hideFooterSelectedRowCount={true}
          hideFooter={true}
          /*@ts-ignore*/
          onEditCellPropsChange={(params) => {
            /*@ts-ignore*/
            setData([params]);
          }}
        />
      </div>
      {/*@ts-ignore*/}
      <MyDrawer open={open} tableSetting={tableSetting} changeTableSetting={onSave} onClose={() => setOpen(false)} />
      {/*@TODO можно вынести в отдельный компонент*/}
      <Button variant="contained" color="success" onClick={() => setOpen((prevState) => !prevState)}>
        Настройки таблицы
      </Button>
    </>
  );
};
//const TaskWrapper = styled.div``;
export default Task1;
